.Divisor{
  width: 80%;
  margin-left: 10%;
}
.botonesUser{
  padding: 40px;
  width: 30%;
  display: flex;
  justify-content: space-around;
  margin: 0 auto;
}
.botonesUser button{
  padding: 10px;
  font-size: large;
}


.seccionInter {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.Register {
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
    rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  width: clamp(300px, 50%, 40%) ;
  margin: 70px;
  padding: 30px;
  justify-content: center;
  align-items: center;

  border-radius: 10px;
  display: flex;
  background-color: rgb(42, 43, 43);
  flex-direction: column;

  color: rgb(255, 255, 255);
}
.Register label {
  width: 80%;
}
.Register input {
  border-radius: 5px;
}
